<template>
    <v-sheet height="90%">
      
      <v-app-bar app color="white">
            <v-tabs align-with-title >
                <v-tab link to="/personal">Inicio</v-tab>
                <v-tab link to="/personal/equipo">Equipo</v-tab>
                <v-tab link to="/personal/reportes">Reportes</v-tab>
            </v-tabs>
            
            <v-autocomplete
            v-model="value"
            :items="items"
            label="Buscar"
            flat
            hide-details
            solo
            prepend-inner-icon="mdi-magnify"
            dense
            light
            style="width:450px; margin-right:10px;"
            @change="route"
          ></v-autocomplete>

            <v-menu offset-y left offset-overflow>
                <template v-slot:activator="{ on, attrs }">
                   <v-btn icon v-bind="attrs" v-on="on">
                      <v-icon>mdi-bell</v-icon>
                  </v-btn>
                </template>
                <v-sheet height="250" width="400" class="d-flex justify-center align-center">
                  <v-sheet height="90%" width="95%">
                    
                    <v-sheet height="10%">
                      <div class="text-body-1 font-weight-bold">Anuncios</div>
                    </v-sheet>
                    <v-sheet height="90%" class="overflow-y-auto">
                        <v-alert border="left" 
                          colored-border 
                          color="primary" 
                          class="text-body-1"
                          elevation="1"
                          v-for="(item, i) in anuncios" :key="i"
                          >
                          {{item.mensaje}}
                        </v-alert> 
                    </v-sheet>
                    
   
                  </v-sheet>
                </v-sheet>
               
              </v-menu>
            <v-btn icon @click="exit">
                <v-icon>mdi-exit-to-app</v-icon>
            </v-btn>
      </v-app-bar>
      <router-view></router-view>

    </v-sheet>
</template>
<script>
import {  mapActions, mapMutations  } from 'vuex'

export default {
  name: 'home',
  data: () => ({
    anuncios: [],
    items: ['dashboard', 'equipo', 'reportes'],
    value: null
  }),
  methods: {
    ...mapActions(['dologout']),
    ...mapMutations(['exitsession']),
    async exit(){
      await this.dologout()
      this.exitsession()
      this.$router.push('/')

    },
    route(param){
      this.$router.push({
        name: param
      })
    }

  },
  computed: {

  }
}
</script>